.tab {
  &-nav {
    list-style: none;
    display: inline-block;
    border-bottom: 1px solid $gray-300;
    &-item {
      float: left;
      margin-bottom: -1px;
      background-color: #fff;
      padding: $tab-item-padding-top-bottom $tab-item-padding-left-right;
      text-align: center;
      font-size: $tab-item-font-size;
      line-height: $tab-item-line-height;
    }
    &-item:hover {
      clear: $blue;
      cursor: pointer;
    }
    &-item.disabled {
      color: $gray-600;
    }
    &-line.is-active {
      border-bottom: $tab-item-border-width solid $blue;
    }
    &-card.is-active {
      border-width: 1px;
      border-style: solid;
      border-color: $gray-300 $gray-300 #fff;
    }
  }
}